.post-topic {
    margin: 0 4px;
}
body {
    font-family: 'Lato', sans-serif;
}
.app-wrapper {
    display: grid;
    justify-content: center;
}

.logo-main,
.recent-posts
{
    display: grid;
    justify-items: center;
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}


$color-gray-one: #666666;
$color-gray-two: #B3B3B3;
$color-gray-three: #D8D8D8;
$color-blue-one: #0079FF;

.no-content {
    color: $color-gray-one;
    font-size: 14px;
    line-height: 17px;
}

@import "./mixins.scss";
@import './searchBar';
@import "./recentposts.scss";
@import "./post.scss";
@import "./home.scss";
@import "./results.scss";
